/* eslint-disable linebreak-style */
/* eslint-disable no-plusplus */
/* eslint-disable max-len */
/* eslint-disable prefer-destructuring */
export default class Tools {
  constructor(currentCanvas, currentFrame, canvasSize) {
    this.colorLayer = '';
    this.currentCanvas = currentCanvas;
    this.canvasSize = canvasSize;
    this.currentFrame = currentFrame;
    this.currentColor = document.getElementById('first-color');
  }

  hexToRgb(hex) {
    this.test = '';
    const res = hex.match(/[a-f0-9]{2}/gi);
    return res && res.length === 3
      ? res.map(v => parseInt(v, 16))
      : null;
  }

  matchStartColor(pixelPos, startPixel) {
    const r = this.colorLayer.data[pixelPos];
    const g = this.colorLayer.data[pixelPos + 1];
    const b = this.colorLayer.data[pixelPos + 2];
    const a = this.colorLayer.data[pixelPos + 3];
    return (r === startPixel[0] && g === startPixel[1] && b === startPixel[2] && a === startPixel[3]);
  }

  colorPixel(pixelPos) {
    const rgba = this.hexToRgb(this.currentColor.value);
    this.colorLayer.data[pixelPos] = rgba[0];
    this.colorLayer.data[pixelPos + 1] = rgba[1];
    this.colorLayer.data[pixelPos + 2] = rgba[2];
    this.colorLayer.data[pixelPos + 3] = 255;
  }


  colorFilling(startX, startY) {
    this.test = '';
    const context = this.currentCanvas.getContext('2d');
    const pixelStack = [[startX, startY]];
    this.colorLayer = context.getImageData(0, 0, this.currentCanvas.width, this.currentCanvas.height);
    const pixelData = context.getImageData(startX, startY, 1, 1).data;

    while (pixelStack.length) {
      let pixelPos; let reachLeft; let reachRight;
      const newPos = pixelStack.pop();
      const coordX = newPos[0];
      let coordY = newPos[1];

      pixelPos = Math.floor((coordY * this.currentCanvas.width + coordX)) * 4;
      while ((coordY >= 0) && this.matchStartColor(pixelPos, pixelData)) {
        pixelPos -= this.currentCanvas.width * 4;
        coordY -= 1;
      }
      pixelPos += this.currentCanvas.width * 4;
      ++coordY;
      reachLeft = false;
      reachRight = false;
      while ((coordY < (this.currentCanvas.height - 1)) && this.matchStartColor(pixelPos, pixelData)) {
        this.colorPixel(pixelPos);

        if (coordX > 0) {
          if (this.matchStartColor(pixelPos - 4, pixelData)) {
            if (!reachLeft) {
              pixelStack.push([coordX - 1, coordY]);
              reachLeft = true;
            }
          } else if (reachLeft) {
            reachLeft = false;
          }
        }

        if (coordX < (this.currentCanvas.width - 1)) {
          if (this.matchStartColor(pixelPos + 4, pixelData)) {
            if (!reachRight) {
              pixelStack.push([coordX + 1, coordY]);
              reachRight = true;
            }
          } else if (reachRight) {
            reachRight = false;
          }
        }

        pixelPos += this.currentCanvas.width * 4;
        coordY += 1;
      }
    }
    context.putImageData(this.colorLayer, 0, 0);
    const currentFr = document.getElementById(`canvas${this.currentFrame}`);
    const currentFrCanvCntx = currentFr.getContext('2d');
    currentFrCanvCntx.clearRect(0, 0, currentFr.width, currentFr.height);
    currentFrCanvCntx.drawImage(this.currentCanvas, 0, 0, currentFr.width, currentFr.height);
  }

  RGBToHex(r, g, b) {
    this.test = '';
    let rColor = r.toString(16);
    let gColor = g.toString(16);
    let bColor = b.toString(16);

    if (rColor.length === 1) { rColor = `0${r}`; }
    if (gColor.length === 1) { gColor = `0${g}`; }
    if (bColor.length === 1) { bColor = `0${b}`; }

    return `#${rColor}${gColor}${bColor}`;
  }

  currentCanvasHandlers() {
    const context = this.currentCanvas.getContext('2d');
    const rect = this.currentCanvas.getBoundingClientRect();
    const imgPen = document.getElementById('img-pen');
    const imgBucket = document.getElementById('img-bucket');
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = 640;
    tempCanvas.height = 640;
    const coordinates = document.getElementById('coordinates');
    let isDrawing = false;
    let mouseDownX = 0;
    let mouseDownY = 0;
    let x = 0;
    let y = 0;

    function currentCanvasClick(e) {
      let startX = e.clientX - rect.left;
      let startY = e.clientY - rect.top;

      if (document.body.className.includes('cursor-bucket')) {
        startX += imgBucket.width;
        startY += imgBucket.height;
      }
      const pixelData = context.getImageData(startX, startY, 1, 1).data;

      if (document.body.className.includes('cursor-color-picker')) {
        const hex = this.RGBToHex(pixelData[0], pixelData[1], pixelData[2]);
        this.currentColor.value = hex;
      } else if (document.body.className.includes('cursor-bucket')) {
        this.colorFilling(startX, startY);
      }
    }
    this.currentCanvas.removeEventListener('click', currentCanvasClick.bind(this));
    this.currentCanvas.addEventListener('click', currentCanvasClick.bind(this));

    // Add the event listeners for mousedown, mousemove, and mouseup
    function currentcanvasMouseDown(e) {
      if (document.body.className.includes('cursor-pen') || document.body.className.includes('cursor-eraser') || document.body.className.includes('cursor-line') || document.body.className.includes('cursor-rectangle')) {
        x = e.clientX - rect.left;
        y = e.clientY - rect.top + imgPen.height;
        x = Math.floor(x / 10) * 10;
        y = Math.floor(y / 10) * 10;
        isDrawing = true;
        if (document.body.className.includes('cursor-line')
          || document.body.className.includes('cursor-rectangle')
        ) {
          mouseDownX = x;
          mouseDownY = y;
          const imgData = context.getImageData(0, 0, 640, 640);
          tempCanvas.getContext('2d').clearRect(0, 0, 640, 640);
          tempCanvas.getContext('2d').putImageData(imgData, 0, 0);
        }
      }
    }
    this.currentCanvas.removeEventListener('mousedown', currentcanvasMouseDown.bind(this));
    this.currentCanvas.addEventListener('mousedown', currentcanvasMouseDown.bind(this));

    function currentCanvasMouseMove(e) {
      this.currentColor = document.getElementById('first-color');
      const scale = 64 / this.canvasSize;
      // context = this.currentCanvas.getContext('2d');
      if (document.body.className.includes('cursor-pen') || document.body.className.includes('cursor-eraser')) {
        if (isDrawing === true) {
          context.beginPath();
          context.rect(x, y, 10 * scale, 10 * scale);
          if (document.body.className.includes('cursor-pen')) {
            context.globalCompositeOperation = 'source-over';
            context.fillStyle = this.currentColor.value;
          } else if (document.body.className.includes('cursor-eraser')) {
            context.globalCompositeOperation = 'destination-out';
            context.fillStyle = 'rgba(255,0,0,0.5);';
          }
          context.fill();
        }
        x = e.clientX - rect.left;
        y = e.clientY - rect.top + imgPen.height;
        x = Math.floor(x / 10) * 10;
        y = Math.floor(y / 10) * 10;
      } else if (document.body.className.includes('cursor-line') && isDrawing === true) {
        x = e.clientX - rect.left;
        y = e.clientY - rect.top + imgPen.height;
        x = Math.floor(x / 10) * 10;
        y = Math.floor(y / 10) * 10;
        const imgData = tempCanvas.getContext('2d').getImageData(0, 0, 640, 640);
        context.clearRect(0, 0, 640, 640);
        context.putImageData(imgData, 0, 0);
        context.beginPath();
        context.globalCompositeOperation = 'source-over';
        context.moveTo(mouseDownX, mouseDownY);
        context.lineWidth = 640 / this.canvasSize;
        context.strokeStyle = this.currentColor.value;
        context.lineTo(x, y);
        context.stroke();
      } else if (document.body.className.includes('cursor-rectangle') && isDrawing === true) {
        x = e.clientX - rect.left;
        y = e.clientY - rect.top + imgPen.height;
        x = Math.floor(x / 10) * 10;
        y = Math.floor(y / 10) * 10;
        const imgData = tempCanvas.getContext('2d').getImageData(0, 0, 640, 640);
        context.clearRect(0, 0, 640, 640);
        context.putImageData(imgData, 0, 0);
        context.beginPath();
        context.globalCompositeOperation = 'source-over';
        context.moveTo(mouseDownX, mouseDownY);
        context.lineWidth = 640 / this.canvasSize;
        context.strokeStyle = this.currentColor.value;
        context.strokeRect(Math.min(x, mouseDownX), Math.min(y, mouseDownY), Math.abs(x - mouseDownX), Math.abs(y - mouseDownY));
        context.stroke();
      }
      const coordX = e.clientX - rect.left;
      const coordY = e.clientY - rect.top;
      coordinates.innerHTML = `X: ${coordX} Y: ${coordY}`;
    }
    this.currentCanvas.removeEventListener('mousemove', currentCanvasMouseMove.bind(this));
    this.currentCanvas.addEventListener('mousemove', currentCanvasMouseMove.bind(this));

    function canvasMouseup() {
      this.currentColor = document.getElementById('first-color');
      const scale = 64 / this.canvasSize;
      if (document.body.className.includes('cursor-pen') || document.body.className.includes('cursor-eraser')) {
        if (isDrawing === true) {
          context.beginPath();
          context.rect(x, y, 10 * scale, 10 * scale);
          if (document.body.className.includes('cursor-pen')) {
            context.globalCompositeOperation = 'source-over';
            context.fillStyle = this.currentColor.value;
          } else {
            context.globalCompositeOperation = 'destination-out';
            context.fillStyle = 'rgba(255,0,0,0.5);';
          }
          context.fill();
        }
      } else if (document.body.className.includes('cursor-line')) {
        context.strokeStyle = this.currentColor.value;
        context.fillStyle = this.currentColor.value;
        context.lineTo(x, y);
        context.closePath();
      }
      x = 0;
      y = 0;
      isDrawing = false;
      const currentFrameCanvas = document.getElementById(`canvas${this.currentFrame}`);
      const currentFrameCanvasContext = currentFrameCanvas.getContext('2d');
      currentFrameCanvasContext.clearRect(0, 0, currentFrameCanvas.width, currentFrameCanvas.height);
      currentFrameCanvasContext.drawImage(this.currentCanvas, 0, 0, currentFrameCanvas.width, currentFrameCanvas.height);
    }

    this.currentCanvas.removeEventListener('mouseup', canvasMouseup.bind(this));
    this.currentCanvas.addEventListener('mouseup', canvasMouseup.bind(this));
  }
}
